@use 'base/variables' as v;
@use 'base/mixins' as m;
.crear {
    #email,#password,#password2 {
        border: 1px solid #6b7280;
    }
}
.login {
    #email,#password {
        border: 1px solid #6b7280;
    }
    margin-top: 5rem;
    .uptask {
        @include m.textoDegradado(v.$cyan);
    }

    input[type="submit"] {
        @include m.boton(v.$cyan, v.$blanco);
    }

    .acciones {
        a {
            color: v.$cyan;
        }
    }
}