@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {
    background-color: v.$grisClaro;

    @include m.tablet {
        display: flex;
        height: 100vh; 
    }

    .principal {
        flex: 1;
        overflow-y: scroll;
    }

    .contenido {
        padding: 2rem;
        .nombre-pagina {
            font-size: 2.8rem;
            text-align: center;
        }
    }

    .formulario {
        input:not([type="submit"]) {
            background-color: v.$blanco;
        }
        input[type="submit"] {
            @include m.boton(v.$indigo, v.$blanco);
            /*padding: 1rem; */
            text-transform: uppercase;
            font-weight: bold;
            font-size: 1.4rem;
        }
    }

    .enlace {
        color: v.$indigo;
        &:hover {
            color: darken(v.$indigo, 20%);
        }
    }
}
.select-teacher {
    background-color: rgb(0, 100, 0);
    color: v.$blanco;
    height: 4rem;
    width: auto;
    border: none;
    margin: 1rem 0 0 0;
    
}
.teacher-header-container {
    display: flex;
   // align-items: end;
    select {
        margin-left: 1rem;
        border-radius: .5rem;
    }
}


.btn-top {
    @include m.boton(v.$indigo, v.$blanco);
    padding: 1rem;
    margin: 1rem 0.2rem 1rem 0.2rem;
    font-size: 1.2rem;
    background-color: rgb(0, 120, 0);
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    border-radius: .5rem;
    height: 4rem;
    width: 4rem !important;
   
        &:hover {
            width: 4.1rem;
            height: 4.1rem !important;
            cursor: pointer;
        }
    
    
}
#search-input {
    margin-bottom: 1rem;
    width: 100%;
    border: none;
    box-shadow: 0 2px 4px rgba(0,0,0,.1);
            padding: calc(v.$separacion / 4);
            border-radius: 1rem;    
    @include m.desktop {
        width: 100%;

    }
}

#search-select {
    
    text-align: center;
    margin: 0 1rem 1rem 0;
    width: auto;
    border: none;
    box-shadow: 0 2px 4px rgba(0,0,0,.1);
            padding: calc(v.$separacion / 4);
            border-radius: 1rem;    
    @include m.desktop {
        width: auto;

    }
    #seccion,option {
        text-align: center;
   //     width: calc(50% - 2rem); /* Ajusta el ancho seg√∫n tus necesidades */
    }
}

