@use 'base/variables' as v;
@use 'base/mixins' as m;


.form {
    width: 100%;

    .slot {
        margin: .5rem;
        align-items: center;
        .div-tag {
            display: flex;
            position: relative;
        }
        .btn-tag {
            right: 0;
            top: 50%;
            
            transform: translate(-40%,-40%);
            position: absolute;
        }
        label {
            //flex: 0 0 10rem;
            display: block;
            margin: .5rem 0;
        }
        input {
            margin: 0.5rem 0 0 0;
            
        }
        input:not([type="checkbox"]) {
            //flex: 1;
            
            width: 100%;
            border: none;
            padding: calc(v.$separacion / 4);
            border-radius: 1rem;    
            
            &:disabled {
                background-color: lighten($color: #000000, $amount: 30%);
                color: v.$blanco;
                cursor: not-allowed;
            }
        }
        input[type="checkbox"] {
            width: 2rem;
            height: 2rem;
        }
    }
    .save-button{
        @include m.boton(v.$indigo, v.$blanco);
        padding: 1rem;
        margin-top: 1rem;
        font-size: 1.2rem;
        background-color: rgb(0, 120, 0);
        transition-property: background-color;
        transition-duration: .3s;
        text-align: center;
        border-radius: .5rem;
        width: 8rem;
        margin: .5rem .5rem;
    }
    input:not([type="submit"]), select {
       // padding: 1rem;
      //  display: block;
        width: 100%;
        //background-color: v.$white;
        border: 1px solid v.$gris;
        border-radius: 1rem;
        
        //margin-bottom: 1rem;
    }
}


/* Horario styles */
.schedule {
    font-family: Arial, sans-serif;
    width: 100%;
    margin: 0 auto;
    margin: 0 0 0 1rem;
    
    //padding: 1rem 0;
  }

  .schedule-table {
    width: auto;
    border-collapse: collapse;
  }

  .schedule-table th,
  .schedule-table td {
    border: 1px solid #ccc;
  }

  .schedule-table th {
    background-color: #f0f0f0;
    font-weight: bold;
  }
  
  .schedule-table td {
    text-align: center;
    background-color: #fff;
    

  }

  .save-button{
    @include m.boton(v.$indigo, v.$blanco);
    padding: 1rem;
    margin-top: 1rem;
    font-size: 1.2rem;
    background-color: rgb(0, 120, 0);
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    border-radius: .5rem;
    width: 8rem;
}

/* Style the tab */
.tab {
    overflow: hidden;
    border: 1px solid #ccc;
    background-color: #f1f1f1;
  }
  
  /* Style the buttons inside the tab */
  .tab button {
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 14px 16px;
    transition: 0.3s;
    font-size: 17px;
  }
  
  /* Change background color of buttons on hover */
  .tab button:hover {
    background-color: #ddd;
  }
  
  /* Create an active/current tablink class */
  .tab button.active {
    background-color: #ccc;
  }
  
  
  /* Style the tab content */
  .tabcontent {
    display: none;
    padding: 6px 12px;
    border: 1px solid #ccc;
    border-top: none;
  }
  .active {
    display: block;
  }

  #profesores {
