html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm-btn {
  display: flex;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.slot img {
  max-width: 20rem;
}

.hidden {
  display: none !important;
}

.mb-1 {
  margin-bottom: 1rem;
}

.mb-2 {
  margin-bottom: 1rem;
}

.mb-3 {
  margin-bottom: 3rem;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
}

/** Media Queries **/
.fx {
  display: flex;
}

.circle-button {
  border-radius: 6rem !important;
  width: 3rem !important;
  height: 3rem !important;
  margin: 0 0 0 0.5rem;
}

.form {
  width: 100%;
}
.form .slot {
  margin: 0.5rem;
  align-items: center;
}
.form .slot .div-tag {
  display: flex;
  position: relative;
}
.form .slot .btn-tag {
  right: 0;
  top: 50%;
  transform: translate(-40%, -40%);
  position: absolute;
}
.form .slot label {
  display: block;
  margin: 0.5rem 0;
}
.form .slot input {
  margin: 0.5rem 0 0 0;
}
.form .slot input:not([type=checkbox]) {
  width: 100%;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.form .slot input:not([type=checkbox]):disabled {
  background-color: #4d4d4d;
  color: #FFFFFF;
  cursor: not-allowed;
}
.form .slot input[type=checkbox] {
  width: 2rem;
  height: 2rem;
}
.form .save-button {
  background-color: rgb(0, 120, 0);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 1rem;
  margin-top: 1rem;
  font-size: 1.2rem;
  background-color: rgb(0, 120, 0);
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 0.5rem;
  width: 8rem;
  margin: 0.5rem 0.5rem;
}
@media (min-width: 768px) {
  .form .save-button {
    width: auto;
  }
}
.form .save-button:hover {
  background-color: #004500;
  cursor: pointer;
}
.form input:not([type=submit]), .form select {
  width: 100%;
  border: 1px solid #6b7280;
  border-radius: 1rem;
}

/* Horario styles */
.schedule {
  font-family: Arial, sans-serif;
  width: 100%;
  margin: 0 auto;
  margin: 0 0 0 1rem;
}

.schedule-table {
  width: auto;
  border-collapse: collapse;
}

.schedule-table th,
.schedule-table td {
  border: 1px solid #ccc;
}

.schedule-table th {
  background-color: #f0f0f0;
  font-weight: bold;
}

.schedule-table td {
  text-align: center;
  background-color: #fff;
}

.save-button {
  background-color: rgb(0, 120, 0);
