.qrcontainer {
  text-align: center;

  .video-preview {
    width: 340px !important; /* Establece el ancho del elemento */
    height: 340px !important;
    margin: 0 auto; /* Centra horizontalmente el elemento */
  }
  canvas {
    width: 1080px !important;
    height: 1080px !important;
  }
  html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    min-height: 100%;
    
    display: flex;
    flex-direction: column;
    color: white;
    
    font-size: 1.6rem;
}
*, *:before, *:after {
    box-sizing: inherit;
}

a {
    text-decoration: none;
}


div {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
}
button {
    background-color: green;
    padding: 1.5rem 4rem;
    color: black;
    margin-top: 2rem;
    font-size: 2rem;
    font-weight: 400;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: block;
    width: 100%;
    margin: 5rem 0;
}
#qr-container {
  margin-top: 20px;
}
h2 {
  color: black;
}

}
.qr-search-form {
  display: flex;
  //align-items: center;
  label {
    margin: 0 0 0 1rem;
  }
  input {
    //margin: 1rem 0 0 0;
    //height: 100%;
   // display: block;
  }
  input[type="submit"]{
    margin: 0 0 0 1rem;
  }
}